/* Home Page */

.index .row {
    text-align: center;
    margin: 2rem auto;
    width: 97.5%;
}

.index h1 {
    margin: 0 auto;
}

.index .landing-container {
    background-color: rgba(14, 14, 14, 0.418);
    z-index: 0;
    padding: 5rem 0;
    margin-top: 2rem;
    margin-bottom: 3rem;
}

.index .landing-container h1 {
    font-size: 8rem;
    font-family: "Quicksand", sans-serif;
    font-weight: bold;
    transform: skewX(-6deg);
    color: #eaf3ff;
    margin: 0.1rem 0;
    text-align: center;
}

.index .landing-container h2 {
    font-size: 2.75rem;
    font-weight: bold;
    color: #ffffff;
    text-align: center;
    z-index: 5;
}

.index .landing-container h4 {
    font-size: 1.25rem;
    transform: skewX(-12deg);
    color: #ffffff;
    text-align: center;
    margin-top: 0.5rem;
}

.index .photo-container {
    margin: 3rem auto;
    z-index: 1;
}

.index .photo-container img {
    box-shadow: 0 0 30px 5px #b47bff98;   
    height: 23rem;
    padding: 0 2rem;
}

.index .about-me-container {
    text-align: center;
    background-color: #202020a2;
    margin: 3rem auto;
    padding: 1rem 2rem;
}

.index .about-me-container p {
    font-size: 1.2rem;
    color: white;
}

.index .skills-container {
    padding: 2rem 2rem;
    margin: 3rem auto 1rem auto;
}

.index .skills-container img {
   width: 50rem;
   margin: 0 auto 2rem auto;
}

/* Projects Page */

.projects .row {
    margin: 0 auto;
    width: 97.5%;
    text-align: center;
}

.projects .project-box {
    text-align: center;
    background-color: #202020a2;
    box-shadow: 0 0 3px 3px #8181814b;
    margin: 2rem 1.2rem;
    padding: 0 2rem 0.5rem 2rem;
}

.projects img { 
    height: 10rem;
    margin: 0rem 0.5rem 2rem 0.5rem;
    float: center;
}

.projects p {
    color: rgb(190, 190, 190);
    font-size: 0.9rem;
    text-align: left;
}

.projects h2 {
    color: white;
    font-size: 2rem;
    width: inherit;
    margin: 2rem auto;
}

.projects h3 {
    color: rgb(255, 255, 255);
    font-size: 1.3rem;
    text-align: left;
}

.projects a p {
    color: rgb(86, 86, 246);
}

.projects a {
    outline: none;
}

.projects iframe {
    padding: 0 0;
    margin: 0 0 1rem 0;
}

/* Contacts Page */

.contact .row {
    width: 95%;
    margin: 0 auto;
    text-align: center;
}

.contact .contact-item {
    margin: 1.5rem 1.5rem;
    padding: 2rem 2rem;
    box-shadow: 0 0 3px 3px #8181814b;
    color: white;
    text-align: center;
}

.contact .box1 {
    background-color: #ff161663;
}
.contact .box2 {
    background-color: #64ffac78;
}
.contact .box3 {
    background-color: #7dc7f178;
}
.contact .box4 {
    background-color: #946dff9b;
}

.contact a {
    color: #f6ec8cdc;
}

.contact img {
    width: 5rem;
    margin: 1rem auto;
}

.contact h2 {
    color: white;
    font-size: 1.3rem;
}

.resume img {
    display: block;
    margin: 1rem auto 3rem auto;
    width: 90%;
}

.title {
    max-width: 30rem;
    font-size: 2rem;
    background-color: #90818136;
    box-shadow: 0 0 10px 5px #ffffffd7;
    padding: 1rem 1rem;
    margin: 3rem auto;
    color: white;
    max-height: 5rem;
    text-align: center;
}